body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins" sans-serif;
}

.wrap{
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
}
.whiteHeader{
  background-color: white;
}
.darkHeader{
  background-color: #252525;
}

header{
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.185);
  width: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 1;
}

button{
  display: flex;
  padding: 10px 10px;
  margin-right: 30px;
  cursor: pointer;
  align-items: center;
  border-radius: 25px;
}

.logo{
 max-height: 35px;
}
.botao{
  width: 25px;
  height: 25px;
}
.btn{
  background-color: #fff;
  border: 2px solid #747272;
}
.darkbtn{
  background-color: #252525;
  border: 2px solid white;
}
.whiteBanner{
  background-color: white;
}
.darkBanner{
  background-color: #252525;
}

#sectionBanner{
  display:  flex;
  height: 100vh;
  padding-top: 90px;
  position: relative;
}

.banner{
  width: 50%;
  background-image: url("/public/img/banner.png");
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
  background-position: center;
}

.texto-banner{
  width: 50%;
  padding: 0px 20px;
  display:  flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fafbfc;
}
.texto-banner p{
  font-size: 12px;
  letter-spacing: 5px;
  color: #022144;
  font-weight: 500;
}
.texto-banner h2{
  font-size: 50px;
  font-weight: 500;
  color: #022144;
}
.texto-banner span{
  font-size: 50px;
  color: #022144;
  font-weight: 900;
}

/*==================== Media 768px ====================*/
@media screen and (max-width:768px){
  .sectionBanner{
    height: 70vh;
  }
  .banner{
    display: none;
  }
  .texto-banner{
    width: 100%;
  }
}

@media screen and (max-width:375px){
  .sectionBanner{
    height: 90vh;
  }
}
.whiteCard{
  border: 1px solid #edecec;
}
.whiteCard p, .whiteCard span{
  color: #8a8fa3;
}
.whiteCard h4{
  color: #32333c;
}


.darkCard{
  border: 1px solid #ffffff0d;
}
.darkCard p, .darkCard span{
  color: #9f9f9f;
}
.darkCard h4{
  color: white;
}

#card{
  width: 33%;
  padding: 70px 30px;
  display: flex;
  flex-direction: column;
  cursor: pointer;

}
#card:hover{
  background-color: #feb633;
  transition: all 1s;
}

#card p, #card span{
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  line-height: 26px;
}

#card p:first-child{
  margin-bottom: 5px;
}
#card span{
  margin-bottom: 20px;
}

#card h4{
  font-weight: 800;
  font-size: 22px;

  padding: 5px 0;
}

@media screen and (max-width:768px){
  #card{
    width: 100%;
  }

  #card p{
    font-size: 20px;
  }
}
.trabWhite{
  background-color: #f6f8fc;
}
.trabDark{
  background-color: #393838;
  color: #CCCCCC;
}

.textoWhite h3{
  color: #32333c;
}
.textoDark h3{
  color: #CCCCCC;
}

#sectionTrab{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 20px;
}

#experiencia h3{
  position: relative;
  font-family: 'Poppins', sans-serif;
  font-size: 45px;
  text-align: center;
  padding: 20px;
  margin-bottom: 30px;
}
h3::before{
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100px;
  height: 4px;
  background-color: #feb633;
}

#experiencia p{
  text-align: center;
  font-family: 'Poppins', sans-serif;
  color: #8a8fa3;
  max-width: 770px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 70px;
}

.cards{
  display: flex;
  justify-content: center;
}

/*==================== Media 768px ====================*/
@media screen and (max-width:768px){
  #sectionTrab{
    font-size: 20px;
  }
  
  .cards{
    flex-direction: column;
  }
}

/*==================== Media 375px ====================*/
@media screen and (max-width:375px){
  #sectionTrab{
    font-size: 16px;
  } 
}
.whiteFooter{
  background-color: white;
}
.darkFooter{
  background-color: #252525;
}

footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-top: 70px;
}

footer .logoRodape{
  width: auto;
  height: auto;
}

footer p{
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 32px;
  color: #8a8fa3;
  text-align: center;
  max-width: 530px;
  margin: 36px auto 42px;
}

.redes{
  width: 100%;
  max-width: 375px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.redes img{
  width: auto;
  height: 45px;
  cursor: pointer;
  padding-right: 10px;
}

.copyright {
  width: 100%;
  font-size: 15px;
  font-weight: 300;
  line-height: 28px;
  color: #8a8fa3;
  padding: 20px 0px;
  text-align: center;
}

.copyright span {
  color: #fcc520;
}
